import React, { useEffect, useState, useRef } from "react";
import MarkNav from "../modules/ContentBlock.js";
import SideBar from "../modules/SideBar.js";
import CommentBlock from "../modules/CommentBlock.js";
import { useParams } from "@reach/router";
import "./BlogPage.css";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { base16AteliersulphurpoolLight } from "react-syntax-highlighter/dist/esm/styles/prism";
import Blog from "../modules/Blog.js";

const BlogPage = () => {
  const { etitle } = useParams();
  const [content, setContent] = useState("");
  const blogContentRef = useRef(null);
  useEffect(() => {
    const fetchContent = async () => {
      if (!etitle) return; // 

      try {
        const response = await fetch("http://localhost:3001/api/blogs");
        const data = await response.json(); // JSON

        // dataetitleblogcontent
        const blogPost = data.find((post) => post.etitle === etitle);

        if (blogPost) {
          setContent(blogPost.blogcontent); // blogcontent
        } else {
          setContent("");
        }
      } catch (error) {
        console.error("Markdown:", error);
        setContent("");
      }
    };

    fetchContent();
  }, [etitle]); // etitle

  const renderers = {
    code: ({ inline, className, children }) => {
      const language = className?.replace("language-", "");
      return inline ? (
        <code>{children}</code>
      ) : (
        <SyntaxHighlighter
          style={base16AteliersulphurpoolLight}
          language={language}
        >
          {children}
        </SyntaxHighlighter>
      );
    },
  };

  return (
    <div className="blogPage">
      <SideBar />
      <Blog content={content} renderers={renderers} ref={blogContentRef} />
      <div className="right-side">
        <MarkNav content={content} scrollContainer={blogContentRef.current} />
        <CommentBlock />
      </div>
    </div>
  );
};

export default BlogPage;
